<script>
    export default{
        name: 'SpriteAnimation'
    }
</script>

<style lang="scss" scoped>
    section{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 100px;
    }
    .main__SvgWrapper{    
        display: flex;
        align-items: center;
        justify-content: space-between;
        z-index: 10;
        position: relative;
        width: 100%;
        .graph__wrapper{
            display: flex;
            justify-content: center;
            svg:nth-child(1){
                width: 100%;
                position: absolute;
                top: 0;
            }
            svg:nth-child(2){
                width: 100%;
                position: absolute;
                bottom: 0;
            }
        }
    }
    #DOGE{
        fill:url(#top_bottom);
        stroke-width: 3px;
    }
    #MoneroBottom{
        fill:url(#bottom);
    }
    #MoneroTop{
        fill:url(#top);
    }
    #XRP{
        top: 60%;
        g path{
            animation: ethAnim 10s infinite;
        }
    }
    #eth{
        top: 40%;
        g{
            polygon{
                animation: ethAnim 10s infinite;
            }
        }
    }
    #Monero, #eth, #XRP, #doge, .icon__btc{
        position: absolute;
        width: 100%;
        animation: allCoinAnim var(--i) linear infinite;
        z-index: 10;
    }
    #Monero{
        top: 20%;
    }
    #doge{
        top: 80%;
    }
    .icon__btc{
        top: 0;
        path{
            stroke-dashoffset: 132;
            stroke-dasharray: 132;
            animation: btcAnim 10s infinite;
        }
    }
    .graphAnim{
        stroke-dashoffset: 766;
        stroke-dasharray: 766;
        animation: graphAnim 10s linear infinite;
    }
    .face{
        animation: faceAnim 2s linear infinite;
    }
    .womanRightHand, .womanHead{
        transform-origin: bottom;
        transform-box: fill-box;
        animation: womanRightHand 2s linear infinite;
    }
    .coinAnim{
        transform-origin: bottom;
        transform-box: fill-box;
        animation: coinAnim 10s linear infinite;
    }
    .manAnimation{
        stroke-dashoffset: 727;
        stroke-dasharray: 727;
        stroke: #5F3791;
        stroke-width: 2px;
        animation: manAnimation 10s linear infinite;
        .colorOne{
            fill-opacity: 0;
            animation: manAnimColorOne 10s linear infinite;
        }
        .colorTwo{
            fill-opacity: 0;
            animation: manAnimColorTwo 10s linear infinite;
        }
        .colorThree{
            fill-opacity: 0;
            animation: manAnimColorThree 10s linear infinite;
        }
    }

    @keyframes allCoinAnim {
        0%{
            opacity: 0;
            transform: translateX(-80%)
        }
        5%{
            opacity: 1;
        }
        95%{
            opacity: 1;
        }
        100%{
            opacity: 0;
            transform: translateX(80%)
        }
    }

    @keyframes manAnimColorOne {
        0%{
            fill-opacity: 0;
        }
        40%{
            fill-opacity: 1;
        }
        80%{
            fill-opacity: 1;
        }
        100%{
            fill-opacity: 0;
        }
    }

    @keyframes manAnimColorTwo {
        0%{
            fill-opacity: 0;
        }
        40%{
            fill-opacity: 1;
        }
        80%{
            fill-opacity: 1;
        }
        100%{
            fill-opacity: 0;
        }
    }

    @keyframes manAnimColorThree {
        0%{
            fill-opacity: 0;
        }
        40%{
            fill-opacity: 1;
        }
        80%{
            fill-opacity: 1;
        }
        100%{
            fill-opacity: 0;
        }
    }

    @keyframes coinAnim {
        0%{
            transform: rotateY(0)
        }
        25%{
            transform: rotateY(-360deg)
        }
        30%{
            transform: rotateY(0)
        }
        35%{
            transform: rotateY(360deg)
        }
        40%{
            transform: rotateY(720deg)
        }
        45%{
            transform: rotateY(1800deg)
        }
        55%{
            transform: rotateY(720deg)
        }
        60%{
            transform: rotateY(360deg)
        }
        100%{
            transform: rotateY(0)
        }
    }

    @keyframes faceAnim {
        0%{
            transform: translateY(-2px)
        }
        50%{
            transform: translateY(5px)
        }
        100%{
            transform: translateY(-2px)
        }
    }

    @keyframes womanRightHand {
        0%{
            transform: rotate(2deg);
        }
        50%{
            transform: rotate(-4deg);
        }
        100%{
            transform: rotate(2deg);
        }
    }

    @keyframes btcAnim {
        to{
            stroke-dashoffset: 0;
        }
    }

    @keyframes manAnimation {
        0%{
            stroke-dashoffset: 727;
        }
        50%{
            stroke-dashoffset: 0;
        }
        100%{
            stroke-dashoffset: 727;
        }
    }

    @keyframes graphAnim {
        0%{
            stroke-dashoffset: 766;
        }
        50%{
            stroke-dashoffset: 384;
        }
        100%{
            stroke-dashoffset: 766;
        }
    }

    @keyframes ethAnim{
        50%{
            transform: translate(0, 0);
            fill: #2f1f5f;
        }
    }

    @media  screen and (max-width:900px){
        .main__SvgWrapper{    
            flex-direction: column;
            align-items: flex-start;
            .graph__wrapper{
                svg:nth-child(1){
                    right: 0;
                    padding-left: 160px;
                }
                svg:nth-child(2){
                    bottom: 200px;
                    right: 0;
                    padding-left: 160px;
                }
            }
        }
    }
</style>